import React, { Component, useEffect, useState, useRef} from 'react';
import {render} from 'react-dom';
import MapGL ,{ StaticMap , FullscreenControl} from 'react-map-gl';
import DeckGL from '@deck.gl/react';
import { MapController, WebMercatorViewport } from '@deck.gl/core';

import './index.css'

import { PublicPolylineLayer }  from './layers/PublicPolylineLayer.js'
import { WeiboSinglePoint } from './layers/WeiboSinglePoint'
import { createChoroplethMap, } from './layers/ChoroplethMapLayer'
import { createVoronoiRegionLayer } from './layers/VoronoiLayer'
import { createRegionMoveLayer } from './layers/RegionMoveLayer'
import { PublicGeoLocationLayer } from './layers/PublicGeoLocationLayer'
import { FrameSelect } from './layers/FrameSelectLayer'
import { CircleLayer } from './layers/CircleLayer'
import GlyphLayer  from './layers/GlyphLayer'
import { INITIAL_VIEW_STATE,
          MAPBOX_TOKEN,
          MAP_STYLE,
          CONSOLE_PRINT  } from './config'
import { createSimulationLayer } from './layers/data-utility/SimulationLayer.js'; 


const Layers = (props) => {
    // console.log( props )
    let { mapId } = props
    let layers = _getLayers( props['params'] , mapId )


  
    CONSOLE_PRINT && (()=>{
      console.log(`Layers of Map: ${props['mapId']} : ` )
      console.table( layers.map((layer)=>{
          if(!layer) return null
          let { id , lifecycle,props } = layer
          let layerName = layer['__proto__']['constructor']['layerName']
          return {
            id,lifecycle,
            layerName,
            longitude: -3.2943888952729092,
            latitude: 53.63605986631115,
          }
      }))
    })() 


    const [viewport, setViewport] = useState()
  
    const mapRef = useRef();
    //     
    useEffect(()=>{
        let { clientWidth, clientHeight } = mapRef.current  
        let state = {
          ...INITIAL_VIEW_STATE,
          width : props['width'] || clientWidth ,
          height : props['height'] || clientHeight
        }
        setViewport( new WebMercatorViewport( state ))
    }, [])
  

  	return (
  		<div id="map" ref={mapRef}>
  			<DeckGL 
  				controller={  props['controller'] }
  				layers={ layers } 
  				initialViewState={ INITIAL_VIEW_STATE }
          getCursor={() => 
            props['params']['framSelect'] && props['params']['framSelect']['cursor'] ? 'crosshair' : 'grab'
          }
          onViewStateChange={ v => {
            let { viewState } = v
            //  viewport
            setViewport( new WebMercatorViewport(viewState) )
          }}
  			>
  				<MapGL
  				  reuseMaps
  				  mapStyle={MAP_STYLE}
  				  preventStyleDiffing={true}
  				  mapboxApiAccessToken={MAPBOX_TOKEN}
  				>
  				</MapGL>
  			</DeckGL>
        <GlyphLayer 
          viewport={viewport}
          { ...props['params']['AnonymousTrajectoryLayer'] } 
        />
  			
        <div
  				id={`tooltip${mapId}`}
  				style={{
  					position: 'absolute',
  					'zIndex': 1,
  					'pointerEvents': 'none'
  				}}
  			>
  			</div>
  		 </div>
  	);
}

// all layers control
const _getLayers = function( params , mapId){
    if(!params)  return 

    let layers = []

    Object.keys( params ).map(( id )=>{
      let param = params[id],
        fn
      switch(id){
        case 'framSelect':
          fn = FrameSelect
          break
        case "ChoroplethMap":
          fn = createChoroplethMap 
          break
        case "VoronoiRegion":
          fn = createVoronoiRegionLayer
          break
        case 'PublicWeiboPointsLayer':
          fn = WeiboSinglePoint
          break
        case 'AnonymousPointsLayer':
          fn = createChoroplethMap
          break
        case 'PublicTrajectoryLayer':
          fn = PublicPolylineLayer
          break
        case 'AnonymousTrajectoryLayer':
          // fn = [ createChoroplethMap , createRegionMoveLayer ]
          break
        case 'PublicPoiPointsLayer':
          fn = PublicGeoLocationLayer
          break
        case  'circle':
          fn = CircleLayer
          break
        default: return
      }

      if( param ){
        if(!fn) return
        if(!Array.isArray( fn ) ){
            if(CircleLayer == fn){
              layers.push( CircleLayer({
                  ...param,
                  radius : param['outerRadius'],
                  nest : 'outer',
              }, mapId ))
              layers.push( CircleLayer({
                  ...param,
                  radius : param['innerRadius'],
                  nest : 'inner'
              },mapId ))
              return 
            }else{
              layers.push( fn.call( null , param , mapId , id ) )
            }
        }else{
          fn.map((_fn)=>{
            layers.push( _fn.call( null , param , mapId ) )

          })
        }
      }
    })

    return layers
}


export default  Layers