
const INITIAL_STATE = {
	currentMapId : -1,
	data : {}
}

export default function update( state = INITIAL_STATE , action) {
	switch( action.type ){
		case 'SWITCH_TO_GLOBAL_MAP':
			return {
				...state,
				currentMapId : action.payload['id'],
				data : action.payload['data']
			}
		default: 
			return state
	}
}

