import {  PolygonLayer, TextLayer } from '@deck.gl/layers';
import { CompositeLayer } from '@deck.gl/core'
import { hexToRGBArray,setTooltip  } from '../util.js'
// import { createGlyph } from './data-utility/GlyphLayer'
// import { createBarChart } from './data-utility/BarChartLayer'

const id = 'VoronoiLayer'

export function createVoronoiRegionLayer( subProps , mapId , layerId ){
	let {
		data = {},
		visible = true,
		strokeOpacity = 1,
		strokeWidth = 1,
		strokeColor = "#5c5050",
		fillOpacity = 0.6,
	} = subProps

	if(!data) return

	return [
		new VoronoiLayer({
			...subProps,
			data,
			visible,
			opacity : strokeOpacity ,
			width : strokeWidth,
			fillOpacity: fillOpacity,
			strokeColor,
			layerId : 'voronois' + mapId,
		}),
		// new createGlyph(subProps , mapId , layerId),
		// new createBarChart(subProps , mapId , layerId)
	]
}

class VoronoiLayer extends CompositeLayer {
	renderLayers() {
	    return [
	      	// 
			new PolygonLayer({
			    id: `${id}-polygon-layer-${this.props['layerId']}`,
				visible :  this.props['visible'],
			    data : this.props['data'],
			    pickable: true,
			    stroked: true,
			    filled: true,
			    wireframe: true,
			    lineWidthMinPixels: 1,
			    extruded: false,
			    getPolygon: d =>  d['polygon'],
				updateTriggers: {
				    getFillColor: this.props['highLightId']
				},
			    getFillColor : d =>  {
			    	let highLightId = this.props['highLightId'],
			    		fillColor = d['fillColor'],
			    		color ,
			    		opacity 

			    	if( highLightId ){
			    		color = ( highLightId != d['id'] ) ?  [255,255, 255, 0 ] : hexToRGBArray('#111111' , 0)
			    	}else{
			    		const defaultColor = '#ffffff'
			    	    color =  d['fillColor'] ?  d['fillColor'] : defaultColor
			    	    opacity =  d['fillColor'] ?  this.props['fillOpacity'] : 0
			    	    // console.log( opacity )
			    	    color =  hexToRGBArray( color  , opacity )
			    	    // console.log( color )
			    	}
			    	return color
			    },
			    getLineColor: hexToRGBArray(this.props['strokeColor'], this.props.opacity ),
			    getLineWidth: this.props['width'],
			    onHover: ({object, x, y}) => {
			    	if(object){
			    		// console.log( object['id'] , object['neighbors'] )
			    	}
			    },
				lineWidthUnits: 'meters'
		  	}),
	   ]
  }
}
VoronoiLayer.layerName = id ;

