import { IconLayer,ScatterplotLayer  } from '@deck.gl/layers';
import { hexToRGBArray,setTooltip  } from '../util.js'

import * as POIIcon from './position.png'
// import * as POIIcon from './icon-atlas.png'

const id = 'PublicGeoLocationLayer' 

export const PublicGeoLocationLayer = (subProps , mapId)=>{
	let { 
		visible = true,
		data = [],
		opacity = 1,
		size = 10
	} = subProps
    return  new ScatterplotLayer({
        id,
        data,
        visible,
		getPosition: d => [d['lng'] , d['lat']],
		getFillColor: d => hexToRGBArray( d['color'] , opacity) ,
	    getLineColor: d => hexToRGBArray( d['color'] , opacity) ,
        getRadius: size,
	    opacity: opacity,
	    pickable: true,
	    stroked: true,
	    filled: true,
	    radiusMinPixels: 1,
	    radiusMaxPixels: 100,
	    lineWidthMinPixels: 1,
	    onHover : info =>  setTooltip(info.object, info.x, info.y , mapId)
	})
}