import { ScatterplotLayer } from '@deck.gl/layers';
import { hexToRGBArray,setTooltip  } from '../util.js'
import { createSimulationLayer } from './data-utility/SimulationLayer'

const id = 'pointLayer'

export const WeiboSinglePoint = ( subProps , mapId)=> {
    const {
      	data = [],
		visible = true,
		radius,
		fillColor,
		strokeColor,
		opacity
    } = subProps 

    return  new ScatterplotLayer({
        id,
        data : data ,
        visible,
	    getPosition: d => d.coordinates,
	    getFillColor: hexToRGBArray(fillColor , 1 ),
	    getLineColor: hexToRGBArray(strokeColor , 1),
        getRadius: radius,
	    opacity: opacity,
	    pickable: true,
	    stroked: true,
	    filled: true,
	    radiusMinPixels: 1,
	    radiusMaxPixels: 100,
	    lineWidthMinPixels: 1,
	    onHover : info =>  setTooltip(info.object, info.x, info.y ,mapId ) 
	})

 }