import React, { Component } from 'react';
import { connect } from 'react-redux'
import Node from './Node.js'
import MyMap from '@/components/map/index.js'
import { Tabs, Select, Button } from 'antd';
import './ResNodePublic.scss'
import LineChart from '@/components/chart/line.js'
import { MyIcon } from '@/components/iconfont'

const { TabPane } = Tabs;
const { Option } = Select;

class ResNodePublic extends Component{
	constructor( props ){
		super(props)
		this.state = {
			mode : 2 || 2,
			subMode : 2
		}
	}


	switchToGlobalMap = () =>{
		let { data,id } = this.props
		this.props.switchToGlobalMap( { data,id } )
	}

	handleCheck = (i) =>{
		console.log( i)
	}
	render(){
		const { props } = this
		const { mode } = this.state
		return (
			<Node {...props} > 
				<div className='public'>
					<div className='left'>
						<div 
							className={ mode == 1 ? 'sub choose' : 'sub'}
							onClick={()=>{this.setState({ mode:1 })}}
						>
							Data Records
						</div>
						<div 
							className={ mode == 2 ? 'sub choose' : 'sub'}
							onClick={()=>{this.setState({ mode:2 })}}
						>
							Data Statistics
						</div>
					</div>
					<div className='right'>
						{
							mode == 1 
							? this.geneRecords()
							: this.geneStatics()
						}
					</div>
				</div>
			</Node>
		)
	}
	geneRecords = () => {
		const { props } = this
		let { data } = props
		let pois = data['datas']['PublicPoiPointsLayer']
		return(

			<div className='record-contain'>
				<div className='head-row record-row'>
					<div className='record-row__check'>
						<input type="checkbox" 
							onClick={this.handleCheck.bind(this,'*')}	
							id='all' />
      					<label htmlFor='all'> </label>
					</div>
					<div className='record-row__title'>
						Select ALL
					</div>
				</div>
				<div className='record-area'>
					{

						pois.map((poi,i)=>(
						<div className='record-row'>

							<div className='record-row__check' key={i}>
								<input type="checkbox" 
									onClick={this.handleCheck.bind(this,i)}
									id={i} />
		      					<label htmlFor={i}> </label>
							</div>
							<div className='record-row__title'>
								{ poi['name'] }
							</div>
						</div>
						))
					}
				</div>
			</div>			
		)
	}
	geneStatics = () => {
		let { subMode } = this.state
		return(
		<div
			style={{
				position:'relative',
				width : '100%',
				height : '100%',
			}}
		>
			<div className='middle'>
				{
					subMode == 1
						? this.geneMa()
						: this.geneChart()
				}
			</div>
			<div className='left'
				style={{
					'float' : 'right'
				}}
			>
				<div 
					className={ subMode == 1 ? 'sub choose' : 'sub'}
					onClick={()=>{this.setState({ subMode:1 })}}
				>
					HeatMap
				</div>
				<div 
					className={ subMode == 2 ? 'sub choose' : 'sub'}
					onClick={()=>{this.setState({ subMode:2 })}}
				>
					Types
				</div>
			</div>
		</div>
		)
	}
	geneMa = () =>{
		const { props } = this
		return (
   			<MyMap {...props['data']} />
		)
	}
	geneChart = ()=>{
		const { props } = this

		return (
			<LineChart {...props['data']} />
		)
	}
}

const mapStateToProps = ({ map }) => {
	return {
	}
}
const mapDispatchToProps = dispatch => {
	return {
		switchToGlobalMap : ( argvs ) => dispatch({
			type : 'SWITCH_TO_GLOBAL_MAP',
			payload : argvs
		})
	}
}

let HOC = connect(mapStateToProps, mapDispatchToProps)(ResNodePublic)
export default HOC