import {  PolygonLayer, TextLayer } from '@deck.gl/layers';
import { CompositeLayer } from '@deck.gl/core'
import { hexToRGBArray,setTooltip  } from '../util.js'

const id = 'ChoroplethMapLayer'

export function createChoroplethMap( subProps , mapId , layerId ) {

	let {
		data = {},
		visible = true,
		strokeOpacity = 1,
		strokeWidth = 1,
		strokeColor = "#5c5050",
		fillOpacity = 0.6,
	} = subProps

	if(!data) return

	console.log( data )
	return new VoronoiLayer({
		...subProps,
		data,
		visible,
		opacity : strokeOpacity ,
		width : strokeWidth,
		fillOpacity: fillOpacity,
		strokeColor,
		layerId : 'choroplethmap' + mapId
	})
}

class VoronoiLayer extends CompositeLayer {
	renderLayers() {
	    return [
	      	// 
			new PolygonLayer({
			    id: `${id}-polygon-layer-${this.props['layerId']}`,
				visible :  this.props['visible'],
			    data : this.props['data'],
			    pickable: true,
			    stroked: true,
			    filled: true,
			    wireframe: true,
			    lineWidthMinPixels: 1,
			    extruded: false,
			    getPolygon: d =>  d['polygon'],
				updateTriggers: {
				    getFillColor: this.props['fillOpacity']
				},
			    getFillColor : d =>  {
			    	return hexToRGBArray( d['fillColor'] ,  this.props['fillOpacity'] )
			    },
			    getLineColor: hexToRGBArray(this.props['strokeColor'], this.props.opacity ),
			    getLineWidth: this.props['width'],
			    onHover: ({object, x, y}) => {
			    	if(object){
			    		// console.log( object['id'] , object['neighbors'] )
			    	}
			    },
				lineWidthUnits: 'meters'
		  	}),
	   ]
  }
}
VoronoiLayer.layerName = id ;

