import { PathLayer } from '@deck.gl/layers';
import { hexToRGBArray } from '../util.js'

const id = 'PublicPolylineLayer'  //  params  id  , 

export const  PublicPolylineLayer = ( subProps ) => {
	const { 
		data = [],
		visible = true,
		strokeWidth,
		strokeColor,
		opacity
	} = subProps

	// data preprocess 
	let paths = []
	data.map((line)=>{
		paths.push( line.map((p)=> [ p['lng'],p['lat']] ))
	})

	// layer 
	return new PathLayer({
	    id,
	    data : paths ,
	    visible,
	    pickable: true,
	    getWidth:  strokeWidth ,
	    getPath: d => d,
	    getColor: hexToRGBArray(strokeColor , opacity),
	    onHover: ({object, x, y}) => {
	    }
	});
}