import { ArcLayer , LineLayer } from '@deck.gl/layers';
import { CompositeLayer } from '@deck.gl/core'
import { hexToRGBArray,setTooltip  } from '../util.js'
import { MyArrowPlotLayer } from './ArrowLayer'
import * as turf from '@turf/turf'

const id = 'RegionMoveLayer' + 'Vector'

export const createRegionMoveLayer = ( subProps ) => {

		const { 
			visible = true,
			data = {},
			vectorWidth = 5,
			arrowColor = '#5c5050',
			vectorColor = '#5c5050',
			arrowOpacity = 1,
			vectorOpacity = 1
		} = subProps

		if(!data['vectors'])   return
		
		return new RegionMoveLayer({
			id,
			...subProps,
			data : data['vectors'].map( (d)=> {
				let { fromPoint,toPoint } = d
				let [ _fromPoint,_toPoint ] = scaleLine( fromPoint,toPoint )
				return {
					...d,
					fromPoint : _fromPoint,
					toPoint : _toPoint
				}
			}),
			width: vectorWidth,
			arrowColor: arrowColor,
			vectorColor: vectorColor,
			arrowOpacity:arrowOpacity,
			vectorOpacity:vectorOpacity,
			visible
		})
}


class RegionMoveLayer extends CompositeLayer {
	renderLayers() {
		return [
	      new LineLayer({
				id: `${id}-line`,
				data : this.props['data'],
				visible:this.props['visible'],
				getSourcePosition: d => [ d.fromPoint['lng'] , d.fromPoint['lat'] ],
				getTargetPosition: d => [ d.toPoint['lng'] , d.toPoint['lat'] ],
				getColor: d => hexToRGBArray( this.props['vectorColor'], this.props['vectorOpacity'] ),
				updateTriggers: {
				getWidth: this.props['width']
				},
				getWidth: d =>  this.props['width'] * d['support'],
				widthScale: 1,
				widthUnits: 'meters'
	      }),
	      new MyArrowPlotLayer({
		        id: `${id}-arrow-scatter`,
		        data: this.props['data'],
				visible:this.props['visible'],
		        getPosition: d => [ d['toPoint']['lng'] , d['toPoint']['lat'] ],
		        getColor: d => hexToRGBArray( this.props['arrowColor'], this.props['vectorOpacity'] ),
		        updateTriggers: {
		          getRadius: this.props['width']
		        },
		        getAngle: d =>  _getAngle( d['fromPoint'] , d['toPoint'] ),
		        getRadius: d =>  this.props['width'] * d['support'],
		        radiusScale: 0.5,
		        widthUnits: 'meters'
	      }),
		]
	}
}

RegionMoveLayer.layerName = id


function _getAngle( p1 , p2 ) {
	
	return 0

	let p1Arr = [p1['lng'] , p1['lat']]
	let p2Arr = [p2['lng'] , p2['lat']]
    let fromPoint =  turf.point( p1Arr )
    let toPoint =  turf.point( p2Arr )
    let angle = turf.bearing(  fromPoint , toPoint )
    // angle -= 180
    let radius = degrees_to_radians( angle )
    // console.log( angle  , radius)
  	var line = turf.lineString([ p1Arr , p2Arr ]);
	var scaleLine = turf.transformScale(line, 3);
	// console.log( scaleLine , line )
    return radius
}


function scaleLine(  p1 , p2 ){
	const scaleRatio  = 0.8 
	let p1Arr = [p1['lng'] , p1['lat']]
	let p2Arr = [p2['lng'] , p2['lat']]
  	let line = turf.lineString([ p1Arr , p2Arr ]);
	let scaleLine = turf.transformScale(line, scaleRatio );

	let [ np1 , np2 ] = scaleLine['geometry']['coordinates']

	return [
		{ lng : np1[0] , lat: np1[1] },
		{ lng : np2[0] , lat: np2[1] }
	]
}

function degrees_to_radians(degrees)
{
  var pi = Math.PI;
  return degrees * (pi/180);
}